@import "normalize";
@import "core";
@import "general";

@import "nav";
@import "banner";
@import "coming-soon";
@import "footer";

@import "subpage";
@import "about";
@import "subpage-contact-button";
@import "bottom-form";

@import "post";
@import "featured-posts";

/* phone-small is default */
$phone-big: 567px;
$phone-hz:567px; /*keep? */
$tablet:768px;
$desktop: 992px;
$l--desktop: 1200px;
$xl--desktop: 1800px;

@media (min-width: #{$phone-hz}) {
	@include nav--phone-hz()
	@include general--phone-hz()
}

@media (min-width: #{$tablet}) {
	@include core--tablet()
	@include general--tablet()
	@include subpage--tablet()

}
@media (min-width: #{$desktop}) {
	@include nav--desktop()
	@include general--desktop()
	@include subpage--desktop()
	@include subpage-contact-button--desktop()
}
@media (min-width: #{$l--desktop}) {
	@include subpage--l--desktop()
}

