.container--bottom-form {
	width:100%;
	display:flex;
	justify-content: center;
	flex-wrap: nowrap;
	background-size:cover;
	background-position: center;
	background-repeat: no-repeat;
	font-family:serif;
	overflow: hidden;
	clip-path:polygon(0% 0%, 100% 5%, 100% 100%, 0 100%);
	padding: 10rem 0 7rem 0;
}
.form-wrapper {
	border: 1px solid grey;
	padding: 5rem 1rem 5rem 1rem;
	displpay:flex;
	flex-direction:column;
	max-width: 400px;
	flex:2;
	form {
		margin: 1rem 0 0 0;
		border-radius: 5px;
		input, textarea {
			width:80%;
			border: 1px solid grey;
			padding: 1rem;
			margin-bottom: 1rem;
			font-size: font-size(text);
		}
		label {
			color: color(white);
			display: block;
			width: 100%;
			padding: 5px;
			font-weight: bold;
			font-size: font-size(text);
		}
	}
}
.form__header {
			padding:0;
		margin: 0;
		color: color(secondary);
		font-family:serif;
		font-weight:bold;
}
.form__header-1 {
font-size: font-size(h1);
}

@mixin bottom-form--desktop(){
	.form-wrapper {
		margin: 0 auto;
	}
}
