.post-container {
	display:flex;
	flex-direction:row;
	justify-content:center;
	margin-top:5rem;
}
.post-wrapper{
	background:#fff;
	max-width:800px;

}
.post-pic {
	width:100%;
	&-top {
	width:100%;
	}
}
.post-date {
	padding: .5rem 0 .5rem 2rem;
	font-size:1rem;
	font-family:courier, sans-serif;
	color:color(grey-accent);

}
.post-title {
	display:flex;
	flex-direction:row;
	justify-content:center;
	font-size:2.5rem;
	font-family:"Comfortaa",sans-serif;
	padding:3rem;
	color:color(tetriary);
}
.post_text {
	font-size:1.2rem;
	font-family:"Cairo", sans-serif;
	color: color(grey-light);
	padding:1rem;
	line-height:2rem;
}
.post_title-centered {
	display:flex;
	justify-content:center;
	font-size:1.4rem;
	font-weight:bold;
	font-family:"Comfortaa", sans-serif;
	color: color(grey-dark);
	padding: 2rem 0rem;
}
.post_prayers {
	&_wrapper {
	padding:1rem;
	background: color(grey-light);
	margin: 0 0 5rem 0;
}
	&_title {
		display:flex;
		justify-content:center;
	font-family:"Comfortaa", sans-serif;
	color: #fff;
	padding: 2rem 0rem;
	font-size:1.4rem;
	font-weight:bold;
	}
	&_ul {
	color: #fff;
	line-height:2rem;
	}
	&_li {
		margin: 0 0 1.2rem 0;
	}
}
