footer {
	max-width: $page-maxwidth;
	margin: 0 auto;
}

/*---------------- Copyright ---------------------*/
.footer-copyright {
	display:flex;
	flex-direction:column;
	justify-content:center;
	align-items:center;
	height: 100px;
	background: color(grey-dark);
	&__text {
		color:color(secondary);
		padding:1rem;
		font-size: .75rem;
	}
}
@mixin footer--tablet(){
	.footer-copyright{
		height:auto;
	}
}

