.fp__blog-link {
	font-size:2rem;
	font-family:'Cairo',sans-serif;
	padding: 0 0 4rem 1rem;
	a {
		color:color(accent);
	}
}
.fp__blog-link a:hover {
	color:color(secondary);
}
.container-fp {
	padding:4rem 1rem 4rem 1rem;
}
.wrapper-fp {
	display:flex;
	flex-direction:column;
	align-items:center;
	flex-wrap:wrap;
}
.card-fp {
	border: 1px solid rgba(0,0,255,.2);
	padding:1rem;
	margin: 1rem;
	max-width:450px;
	font-family:'Comfortaa',san-serif;
	display:flex;
	flex-direction:column;
	a {color:color(grey-dark);
	}
	&__img {
		width:100%;
		overflow:hidden;
	}
	&__title {
		color:color(primary);
		font-family:'Cairo',sans-serif;
		margin: .5rem 0 0 0;
	}
	&__text {
		margin: 1rem 0;
	}
	&__date {
		font-family:'Cairo',sans-serif;
		color:color(primary);
		font-weight:bold;
		text-align:right;
	}
}
@mixin featured-posts--tablet() {
	.wrapper-fp {
		flex-direction:row;
	}
	.card-fp {
	max-width:250px;
	height:22rem;
	&__img {
		height: 160px;
	}
}
}
