
.about-wrapper {
	display:flex;
	justify-content:center;
	padding: 10rem 1rem;
}
.about-card {
	max-width:600px;
	display:flex;
	flex-direction:column;
	padding: 0 0 10rem 0;
}
.about-image {
	max-width:600px;
}
.about-title {
	font-size:2rem;
	color:color(secondary);
	font-family:"Comfortaa", cursive;
	padding: 2rem 0 .5rem 0;
}
.about-text {
	font-family:"Cairo", sans-serif;
	line-height:2rem;
	font-size:1.25rem;
	color:color(dark);
}
